:root {
  --font-family: "DM Sans", sans-serif;
  --black: #111;
  --black-1: #202024;
  --violet: #4f2ee8;
  --yellow: #eac645;
  --white: #fff;
  --light-bg: #f6f6f6;
  --transition: 250ms linear;
}

body {
  font-family: var(--font-family);
  color: var(--black);
  background-color: var(--light-bg);
  font-size: 18px;
  font-weight: 700;
}

.container {
  margin: 0 auto;
  padding: 0 20px;
}

.wrapper {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

@media screen and (min-width: 375px) {
  .container {
    width: 375px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1400px) {
  .container {
    width: 1400px;
    padding: 0 24px;
  }
}

